import { Component, OnInit } from '@angular/core';
import { Animation, AnimationController, ModalController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';
import { TouchSequence } from 'selenium-webdriver';


@Component({
  selector: 'app-order-status',
  templateUrl: './order-status.page.html',
  styleUrls: ['./order-status.page.scss'],
})
export class OrderStatusPage implements OnInit {

  order: any =[];
  user: any;
  orderid: any;
  product: any;
  fname: any;
  lname: any;
  classi1: any;
  classi2: any;
  quant: any;
  num: any;
  brgy: any;
  street: any;
  con: any;
  id: any;

  constructor( 
    private animatioCntrl: AnimationController,
    private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService,
  ) { }

  ngOnInit() {
  }
  ionViewDidEnter(){
    this.order=[];
    this.orderid=this.navParamService.getNavData();
    console.log(this.orderid);
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
      this.id=res.user_id;
    return new Promise(resolve =>{
      console.log(this.orderid);
      let body = {
        aksi: 'get_orders_costumer',
        orderid: this.orderid,
        id: this.id
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.result);
           for(let datas of res.result){ 
            this.order.push(datas);
          }
          this.user=res.user;
          this.fname=this.user.first_name;
          this.lname=this.user.last_name;
          this.num=this.user.contact_number;
          this.brgy=this.user.barangay_name;
          this.street=this.user.street;
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  });
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }  
 
}
